.input_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }
  
  input[type="text"] {
    width: 60%;
    padding: 10px;
    border: 1px solid lightgray;
    border-radius: 5px;
    font-size: 18px;
    outline: none;
    box-shadow: 0 0 5px lightgray;
  }
  input[type="text"]::-webkit-input-placeholder { 
    font-style: italic;
    color: lightgray;
  }
  
  input[type="text"]::-moz-placeholder { 
    font-style: italic;
    color: lightgray;
  }
  
  input[type="text"]::-ms-input-placeholder {  
    font-style: italic;
    color: lightgray;
  }
  
  
  .btn_container {
    display: flex;
    justify-content: center;
  }
  
  button {
    width: 20%;
    padding: 10px;
    border: 1px solid rgb(0, 0, 0);
    border-radius: 5px;
    background-color: black;
    color: white;
    font-size: 18px;
    cursor: pointer;
    outline: none;
    box-shadow: 0 0 5px lightblue;
  }
  
  button:hover {
    background-color: deepskyblue;
    border-color: deepskyblue;
  }